import './App.css'; 
import { useState } from 'react';
// import { ethers } from 'ethers';
import contractABI from './Vendor-Contract-ABI.json';

function App() {
  const ethers = require("ethers");
  const contractAddress = '0x19bb2c97fC2bDa6B0F9D880C9ED03aC9fDa75e96'; 

  let provider = ((window.ethereum != null) ? new ethers.providers.Web3Provider(window.ethereum) : ethers.providers.getDefaultProvider());
  //let provider = new ethers.providers.Web3Provider(window.ethereum)
  let contract = new ethers.Contract(contractAddress, contractABI, provider); 
  let signer;

  const [walletAddress, setWalletAddress] = useState("");
  const [walletBalance, setWalletBalance] = useState("");
  const [withdrawResp, setWithdrawResp] = useState("");
  
  // connect metamask
  const connect = async () => {
      // display chain ID
      if (typeof window.ethereum !== 'undefined') { 
        // get network chain id
        const network = await provider.getNetwork();
        const chainId = network.chainId;
        console.log(chainId); 
        if (chainId === 5) { 
          // metamask require request permission to connect user accounts
          await provider.send("eth_requestAccounts", []);
          signer = await provider.getSigner();
          const accountAddress = await signer.getAddress();
          console.log("account address: ", accountAddress);
          setWalletAddress("Account Address: " + accountAddress);
          
          // get balance from connected account 
          // const getBalance = async () => {
          const balance = await signer.getBalance();
          const converToEth = 1e18;
          console.log("account's balance: ", balance.toString() / converToEth);
          setWalletBalance("Account Balance: " + balance.toString() / converToEth);
        } else {
          setWalletAddress("metamask network connection not correct");
        }
      } else {
        setWalletAddress("metamask is not install");
      }    
  }

// withdraw eTH from vendor contract address
const withdraw = async () => {
    try {
      await provider.send("eth_requestAccounts", []);
      signer = await provider.getSigner();
      await contract.connect(signer).withdraw();  
      setWithdrawResp("transaction completed")  
    } catch (error) {
      if (error.message.includes("owner")) {
        setWithdrawResp("only owner can do this function");        
      } else if (error.message.includes("has not balance")) {
        setWithdrawResp("no balance to withdraw")
      } else {
        console.log(error.message)
      }
    }
  }
  

// function App() {
  return (
    <div className="App">
      <header className="App-header">        
        <h2> Token sell vendor machine. </h2>        
        
        <section class="card card1">
         <ol>
            <h5> Procedure :</h5>
            <li>Hit <green>[CONNECT to Metamask]</green> button and select Goerli test network </li>
            <li>Hit <green>[Withdraw]</green> button to withdraw ETH balances from the contract </li>
        </ol>
        </section>
       
        <br></br>
        <br></br>
                  
          <div className="App-button-box">
            <button onClick={connect}>CONNECT to Metamask</button>
            <h5> {walletAddress} </h5> 
            <h5> {walletBalance} </h5>               
          </div>
                    
          <div className="App-button-box">  
            <button onClick={withdraw}>Withdraw</button>  
            <output id="withdrawInd" name="withdrawprocess"></output> 
            <h5> {withdrawResp} </h5>                  
          </div>               
      
        </header>      
    </div>
  )
}
export default App;